import React, { useState, useContext } from 'react';
import './login.scss';
import { useNavigate } from 'react-router-dom';
import { socketService } from '../../services/Socket.service'
import { StoreContext } from '../../context/StoreContext'
import { patientService } from '../../services/Patient.service'

function Login() {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const navigate = useNavigate();
    const [error, setError] = useState(null);
    const { sharingData, getUsername, getLogin } = useContext(StoreContext)

    const login = (e) => {
        if (username !== '' && password !== '') {
            socketService.login(username, password, result => {
                if (result == 'patient') {
                    getLogin('true')
                    sharingData(username)
                    navigate('/bc-healthcare-client/patient')
                }
                else if (result == 'doctor') {
                    getLogin('true')
                    getUsername(username)
                    navigate('/bc-healthcare-client/Home')
                }
                else {
                    setError(true)
                }
            })
        }
        else {
            setError(true)
        }

        e.preventDefault();
    }

    return (
        <div className="bg-view-img">
            <div className="content-view">
                <div className="title-text font-gg">Welcome to BC Healthcare network</div>
                <div className="desc-text font-gg">Login with your provided account to access BC system.</div>
                {
                    error ? <div className="err-text font-gg">Wrong user name or password</div> : null
                }
                <input
                    type="text"
                    onChange={(e) => { setUsername(e.target.value) }}
                    placeholder='User name'
                    className="text-input font-gg"
                    defaultValue={''}
                />
                <input
                    type="password"
                    onChange={(e) => { setPassword(e.target.value) }}
                    placeholder='Password'
                    className="text-input font-gg"
                    defaultValue={''}
                />
                <button className='main-button font-gg margin-b-8' onClick={login} style={{ marginLeft: 8 }}><div>Login</div></button>
            </div>
        </div>
    )
}

Login.propTypes = {

}

export default Login
