import React, { useState, useContext } from 'react';
import './prescription.scss';
import { useNavigate } from 'react-router-dom';
import PrescriptionModel from '../../models/Prescription.model'
import { TimeUtil } from '../../utils/Time.util'
import { socketService } from '../../services/Socket.service'
import { StoreContext } from '../../context/StoreContext'
import MedicationModel from '../../models/Medication.model';

function Prescription() {
    const navigate = useNavigate();
    const [prescriptiomName, setPrescriptiomName] = useState('');
    const [medicationClass, setMedicationClass] = useState('');
    const [medicationName, setMedicationName] = useState('');
    const [route, setRoute] = useState('');
    const [schedule, setSchedule] = useState('');
    const { sharing, sharingData, username } = useContext(StoreContext)
    const [listMedication, setListMedication] = useState([])
    var today = new Date();
    var date = today.getDate() + '-' + (today.getMonth() + 1) + '-' + today.getFullYear();
    const addPrescription = (e) => {
        if (prescriptiomName !== '') {
            let prescription = new PrescriptionModel()
            prescription.name = prescriptiomName
            prescription.ID = TimeUtil.currentUTCTimestamp()
            prescription.doctor = username
            prescription.patientID = sharing
            prescription.createdDate = date
            addMoreMedication()
            prescription.medications = listMedication
            if (listMedication.length != 0){
                socketService.addNewPrescription(prescription.toJson)
                sharingData(sharing)
                navigate('/bc-healthcare-client/patientdetail')
            }
        }
        else (alert("Please Enter Prescription Name!!!"))
    }

    const addMoreMedication = (e) => {
        if (medicationClass !== '' && medicationName !== '' && route !== '' && schedule !== '') {
            let medication = new MedicationModel()
            medication.ID = TimeUtil.currentUTCTimestamp()
            medication.class = medicationClass
            medication.name = medicationName
            medication.route = route
            medication.schedule = schedule
            if (listMedication) {
                listMedication.push(medication)
            }
            else {
                setListMedication(medication)
            }
            alert("Add Successful")
        }
        else if (listMedication.length == 0){
            alert("Please Enter Medication")
        }
        document.getElementById('medication-class-value').value = ''
        document.getElementById('medication-name-value').value = ''
        document.getElementById('route-value').value = ''
        document.getElementById('schedule-value').value = ''
        setMedicationName(document.getElementById('medication-class-value').value)
    }

    return (
        <div className="prescription">
            <div className="prescription-container">
                <div className="prescription-header">
                    <h1 className="prescription-header-title">
                        New Prescription
                    </h1>
                </div>
                <div className="prescription-panel">
                    <table className="prescription-panel-table">
                        <tbody>
                            <tr className="prescription-panel-table-header">
                                <td colSpan={2} className="prescription-panel-table-header-title">
                                    PRESCRIPTION
                                </td>
                            </tr>
                            <tr className="prescription-name">
                                <td className="prescription-name-title"><label htmlFor="prescription-name-value" >Prescription name</label></td>
                                <td className="prescription-name-value"><input id="prescription-name-value" onChange={(e) => { setPrescriptiomName(e.target.value) }} type="text" className="prescription-name-value input" /></td>
                            </tr>
                            <tr className="prescription-doctor">
                                <td className="prescription-doctor-title"><label >Doctor</label></td>
                                <td className="prescription-doctor-value"><label className="input">{username}</label></td>
                            </tr>
                        </tbody>
                    </table>
                    <table className="medication-dose">
                        <tbody>
                            <tr className='medication-dose-header'>
                                <td colSpan={2} className="medication-dose-header-title">
                                    MEDICATION
                                </td>
                            </tr>
                            <tr className="medication-class">
                                <td className="medication-class-title"><label htmlFor="medication-class-value" >Medication class</label></td>
                                <td className="medication-class-value"><input id="medication-class-value" onChange={(e) => { setMedicationClass(e.target.value) }} className="input" /></td>
                            </tr>
                            <tr className="medication-name">
                                <td className="medication-name-title"><label htmlFor="medication-name-value">Medication name</label></td>
                                <td className="medication-name-value"><input id="medication-name-value" onChange={(e) => { setMedicationName(e.target.value) }} className="input" /></td>
                            </tr>
                            <tr className="route">
                                <td className="route-title"><label htmlFor="route-value">Route</label></td>
                                <td className="route-value"><input id="route-value" onChange={(e) => { setRoute(e.target.value) }} className="input" /></td>
                            </tr>
                            <tr className="schedule">
                                <td className="schedule-title"><label htmlFor='schedule-value'>Schedule</label></td>
                                <td className="schedule-value"><input id="schedule-value" onChange={(e) => { setSchedule(e.target.value) }} className="input" /></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="btn">
                    <button className="sec-button" onClick={() => navigate('/bc-healthcare-client/patientdetail')}>Back</button>
                    <button className="main-button margin-l-8" onClick={addPrescription}>Add Prescription</button>
                    <button className="main-button margin-l-8" onClick={addMoreMedication}>Add Medication</button>
                </div>
                <table className="medication-dose-table">
                    <tbody className="medication-dose-table-body">
                        <tr className='medication-dose-table-header'>
                            <td colSpan={4} className="medication-dose-table-header-title">
                                MEDICATION
                            </td>
                        </tr>
                        <tr className='medication-detail-title'>
                            <th className='medication-dose-class-title'>Medication Class</th>
                            <th className='medication-dose-name-title'>Medication Name</th>
                            <th className='medication-dose-route-title'>Route</th>
                            <th className='medication-dose-schedule-title'>Schedule</th>
                        </tr>
                        {listMedication ? listMedication.map(e => {
                            return (
                                <tr className='medication-dose-content' key={e.ID}>
                                    <td className='medication-dose-class-value'>{e.class}</td>
                                    <td className='medication-dose-name-value'>{e.name}</td>
                                    <td className='medication-dose-route-value'>{e.route}</td>
                                    <td className='medication-dose-schedule-value'>{e.schedule}</td>
                                </tr>
                            )
                        }) :
                            <tr className='medication-dose-content' >
                                <td className='medication-dose-class-value'></td>
                                <td className='medication-dose-name-value'></td>
                                <td className='medication-dose-route-value'></td>
                                <td className='medication-dose-schedule-value'></td>
                            </tr>
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}

Prescription.propTypes = {

}

export default Prescription