import React, { useState, useContext, useEffect } from 'react';
import './PatientViewDetail.scss';
import { useNavigate } from 'react-router-dom';
import { StoreContext } from '../../context/StoreContext'
import { doctorService } from '../../services/Doctor.service'
import { socketService } from '../../services/Socket.service'
import PrescriptionModel from '../../models/Prescription.model'

function PatientView() {
    const navigate = useNavigate();
    const { sharing, getLogin, getPatient, getIdPrescription, getListPrescription } = useContext(StoreContext)
    const [patient, setPatient] = useState([]);
    const [doctors, setDoctors] = useState([]);
    const [prescription, setPrescription] = useState([]);

    useEffect(() => {
        socketService.getPatientDetail(sharing, result => {
            if (result) {
                setPatient(result)
            }

            if (result.prescriptions) {
                setPrescription(result.prescriptions.map(e => {
                    return PrescriptionModel.initFromJson(e)
                }))
            }
        })
    }, [])

    useEffect(() => {
        socketService.getListDoctors(doctors => {
            doctorService.doctors = doctors
            setDoctors(doctorService.doctors)
        })
    }, [])

    const logOut = async (e) => {
        await getLogin('false');
        navigate('/bc-healthcare-client/login')
    }

    const toPrescription = async (e) => {
        navigate('/bc-healthcare-client/patientprescription')
    }

    const changePass = async (e) => {
        getPatient(patient)
        navigate('/bc-healthcare-client/editpatient')
    }

    const toDetailPrescription = (e) => {
        getIdPrescription(e)
        getListPrescription(prescription)
        navigate('/bc-healthcare-client/patientprescriptiondetail')
    }

    return (
        <div className="patient-view">
            <div className="patient-view-container">
                <div className="patient-view-container-header">
                    <div className='patient-view-container-header-bar'>
                        <h2 className="patient-view-title">Patient - {patient.name}</h2>
                        <button className='sec-button' onClick={changePass}>Change Password</button>
                    </div>
                    <button className='sec-button' onClick={logOut}>Logout</button>
                </div>
                <div className="patient-view-panel">
                    <div className="patient-view-panel-left">
                        <h1 className='patient-view-panel-left-title'>Patient Information</h1>
                        <table className='patient-view-panel-left-table'>
                            <tbody >
                                <tr className="patient-id">
                                    <td className="content">Patient ID: {patient.ID}</td>
                                </tr>
                                <tr className="patient-gender">
                                    <td className='content'>Gender: {patient.gender}</td>
                                </tr>
                                <tr className="patient-dob">
                                    <td className='content'>Birthday: {patient.DOB}</td>
                                </tr>
                                <tr className="patient-address">
                                    <td className='content'>Address: {patient.address}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="patient-view-panel-middle">
                        <div className="doctor">
                            <div className="doctor-bar">
                                <h1 className='doctor-bar-title'>Prescriptions</h1>
                            </div>
                            <table className="doctor-list">
                                <tbody>
                                    {prescription ? prescription.map(e => {
                                        return (
                                            <tr onClick={(event) => toDetailPrescription(e.ID)}  className="doctor-name" key={e.ID}>
                                                <td>{e.name}</td>
                                            </tr>
                                        )
                                    }) : 'No data'}
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div className="patient-view-panel-right">
                        <div className="doctor">
                            <div className="doctor-bar">
                                <h1 className='doctor-bar-title'>List Doctor</h1>
                            </div>
                            <table className="doctor-list">
                                <tbody>
                                    {doctors ? doctors.map(e => {
                                        return (
                                            <tr className="doctor-name" key={e.username}>
                                                {e.username === patient.assignedDoctor ?
                                                    <td className='active'>{e.name}</td>
                                                    :
                                                    <td>{e.name}</td>
                                                }
                                            </tr>
                                        )
                                    }) : 'No data'}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

PatientView.propTypes = {

}

export default PatientView
