import React, { useContext, useState, useEffect } from 'react';
import './patientPrescription.scss';
import { useNavigate } from 'react-router-dom';
import { StoreContext } from '../../context/StoreContext'
import { patientService } from '../../services/Patient.service'
import { socketService } from '../../services/Socket.service'
import PrescriptionModel from '../../models/Prescription.model'

function PatientPrescription() {
    const navigate = useNavigate();
    const { sharing, getIdPrescription, getListPrescription, getPatient } = useContext(StoreContext)

    const [patient, setPatient] = useState([]);

    const [prescription, setPrescription] = useState([]);

    useEffect(() => {
        socketService.getPatientDetail(sharing, result => {
            if (result) {
                setPatient(result)
                getPatient(result)
            }
            if (result.prescriptions) {
                setPrescription(result.prescriptions.map(e => {
                    return PrescriptionModel.initFromJson(e)
                }))
            }
        })
    }, [])

    const toDetailPrescription = (e) => {
        getIdPrescription(e)
        getListPrescription(prescription)
        navigate('/bc-healthcare-client/patientprescriptiondetail')
    }

    return (
        <div className="patient-prescription">
            <div className="patient-prescription-container">
                <div className="patient-prescription-container-header">
                    <h2 className="patient-prescription-title">{patient.name}</h2>
                    <button className='btn-back' onClick={() => navigate('/bc-healthcare-client/patient')}>Back</button>
                </div>
                <div className="patient-prescription-panel">
                    <div className="patient-prescription-panel-left">
                        <h1 className='patient-prescription-panel-left-title'>Patient Infomation</h1>
                        <table className='patient-prescription-panel-left-table'>
                            <tbody>
                                <tr className="patient-id">
                                    <td className="content">Patient ID: {patient.ID}</td>
                                </tr>
                                <tr className="patient-gender">
                                    <td className='content'>Gender: {patient.gender}</td>
                                </tr>
                                <tr className="patient-dob">
                                    <td className='content'>Birthday: {patient.DOB}</td>
                                </tr>
                                <tr className="patient-address">
                                    <td className='content'>{patient.address}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className="patient-prescription-panel-right">
                        <div className="patient-prescription">
                            <div className="patient-prescription-bar">
                                <h1 className='patient-prescription-bar-title'>Prescription</h1>
                            </div>
                            <table className="patient-prescription-list">
                                <tbody>
                                    {prescription ? prescription.map(e => {
                                        return (
                                            <tr onClick={(event) => toDetailPrescription(e.ID)} className="prescription-name" key={e.ID}>
                                                <td>{e.name}</td>
                                            </tr>
                                        )
                                    })
                                        : "No data"}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

PatientPrescription.propTypes = {

}

export default PatientPrescription