import React, { useState, useEffect, useContext } from 'react';
import './editPatient.scss';
import { useNavigate } from 'react-router-dom';
import { StoreContext } from '../../context/StoreContext'
import { socketService } from '../../services/Socket.service'

function EditPatient() {
    const navigate = useNavigate();
    const { patient } = useContext(StoreContext)
    const [currentPass, setCurrentPass] = useState();
    const [newPass, setNewPass] = useState();
    const [confirmPass, setConfirmPass] = useState();
    const ID = patient.ID;

    const Edit = async () => {
        if (newPass == undefined && confirmPass == undefined) {
            alert('Please Enter Information')
        }
        else if (newPass == confirmPass) {
            socketService.editPatient(ID, currentPass, newPass)
            alert('Successful!')
            navigate('/home')
        }
        else
            alert('New Password and Confirm Password do not match')
    }

    return (
        <div className="edit-patient">
            <div className="edit-patient-title">
                Change password
            </div>
            <div className="edit-patient-body">
                <p className="edit-patient-body-name-title">Patient full name</p>
                <label type="text" placeholder='Full name' className="input">{patient ? patient.name : ""}</label>
                <p className="edit-patient-body-id-title">ID</p>
                <label type="text" placeholder='ID' className="input">{patient ? patient.ID : ""}</label>
                <p className="edit-patient-body-current-password-title">Current Password</p>
                <input type="password" placeholder='Current Password' className="input" onChange={(e) => setCurrentPass(e.target.value)} />
                <p className="edit-patient-body-new-password-title">New Password</p>
                <input type="password" placeholder='New Password' className="input" onChange={(e) => setNewPass(e.target.value)} />
                <p className="edit-patient-body-confirm-new-password-title">Confirm New Password</p>
                <input type="password" placeholder='Confirm New Password' className="input" onChange={(e) => setConfirmPass(e.target.value)} />
                <div className="btn">
                    <button className="sec-button margin-r-8" onClick={() => navigate('/patient')}>Back</button>
                    <button className="main-button" onClick={Edit}>Save</button>
                </div>
            </div>
        </div>
    )
}

EditPatient.propTypes = {

}

export default EditPatient
