import React, { useContext, useState, useEffect } from 'react';
import './patientDetail.scss';
import { useNavigate } from 'react-router-dom';
import { StoreContext } from '../../context/StoreContext'
import { patientService } from '../../services/Patient.service'
import { socketService } from '../../services/Socket.service'
import PrescriptionModel from '../../models/Prescription.model'

function PatientDetail() {
    const navigate = useNavigate();
    const { sharing, getIdPrescription, getListPrescription, getPatient } = useContext(StoreContext)

    const [patients, setPatients] = useState([]);
    const [prescription, setPrescription] = useState([]);

    useEffect(() => {
        socketService.getPatientDetail()
        const getAllListpatient = async () => {
            setPatients(patientService.patients)
        }
        getAllListpatient()
    }, [])

    useEffect(() => {
        socketService.getPatientDetail(sharing, result => {
            if (result.prescriptions) {
                setPrescription(result.prescriptions.map(e => {
                    return PrescriptionModel.initFromJson(e)
                }))
            }
        })
    }, [])

    const toDetailPrescription = (e) => {
        patients.map(patient => {
            if (patient.ID === sharing) {
                getPatient(patient)
            }
        })
        getIdPrescription(e)
        getListPrescription(prescription)
        navigate('/bc-healthcare-client/prescriptiondetail')
    }

    const patientName = () => {
        const result = patients.find(e => e.ID == sharing)
        if (result) return result.name
        return '---'
    }

    return (
        <div className="detail">
            <div className="detail-container">
                <div className="detail-container-header">
                    <h2 className="detail-title">{patientName()}</h2>
                    <button className='sec-button' onClick={() => navigate('/bc-healthcare-client/home')}>Back</button>
                </div>
                <div className="patient-detail-panel">
                    <div className="patient-detail-panel-left">
                        <h1 className='patient-detail-panel-left-title'>Patient Infomation</h1>
                        <table className='patient-detail-panel-left-table'>
                            {
                                patients.map(e => {
                                    if (e.ID === sharing) {
                                        return (
                                            <tbody key={e.ID}>
                                                <tr className="patient-id">
                                                    <td className="content">Patient ID: {e.ID}</td>
                                                </tr>
                                                <tr className="patient-gender">
                                                    <td className='content'>Gender: {e.gender}</td>
                                                </tr>
                                                <tr className="patient-dob">
                                                    <td className='content'>Birthday: {e.DOB}</td>
                                                </tr>
                                                <tr className="patient-address">
                                                    <td className='content'>{e.address}</td>
                                                </tr>
                                            </tbody>
                                        )
                                    }
                                })
                            }
                        </table>
                    </div>
                    <div className="patient-detail-panel-right">
                        <div className="patient-prescription">
                            <div className="patient-prescription-bar">
                                <h1 className='patient-prescription-bar-title'>Prescription</h1>
                                <button className="sec-button margin-t-8 margin-l-8" onClick={() => navigate('/bc-healthcare-client/prescription')}>New Prescription</button>
                            </div>
                            <table className="patient-prescription-list">
                                <tbody>
                                    {prescription ? prescription.map(e => {
                                        return (
                                            <tr onClick={(event) => toDetailPrescription(e.ID)} className="prescription-name" key={e.ID}>
                                                <td>
                                                    <div>{e.name}</div>
                                                    <div className='create-date-text'>{e.createdDate}</div>
                                                </td>
                                            </tr>
                                        )
                                    })
                                        : "No data"}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

PatientDetail.propTypes = {

}

export default PatientDetail