import React, { useState, useEffect, useContext } from 'react';
import './editDoctor.scss';
import { useNavigate } from 'react-router-dom';
import { StoreContext } from '../../context/StoreContext'
import { doctorService } from '../../services/Doctor.service'
import { socketService } from '../../services/Socket.service'

function EditDoctor() {
    const navigate = useNavigate();
    const { username } = useContext(StoreContext)
    const [doctor, setDoctor] = useState([]);
    const [currentPass, setCurrentPass] = useState();
    const [newPass, setNewPass] = useState();
    const [confirmPass, setConfirmPass] = useState();

    const Edit = async () => {
        if (newPass == undefined && confirmPass == undefined) {
            alert('Please Enter Information')
        }
        else if (newPass == confirmPass) {
            socketService.editDoctor(username, currentPass, newPass)
            alert('Successful!')
            navigate('/home')
        }
        else
            alert('New Password and Confirm Password do not match')
    }

    useEffect(() => {
        socketService.getListDoctors(doctors => {
            doctorService.doctors = doctors
            doctorService.doctors.map(e => {
                if (e.username == username) {
                    setDoctor(e)
                }
            })
        })
    }, [])

    return (
        <div className="edit-doctor">
            <div className="edit-doctor-title">
                Edit information
            </div>
            <div className="edit-doctor-body">
                <p className="edit-doctor-body-name-title">Doctor full name</p>
                <label type="text" placeholder='Full name' className="input">{doctor ? doctor.name : ""}</label>
                <p className="edit-doctor-body-username-title">Username</p>
                <label type="text" placeholder='Username' className="input">{doctor ? doctor.username : ""}</label>
                <p className="edit-doctor-body-current-password-title">Current Password</p>
                <input type="password" placeholder='Current Password' className="input" onChange={(e) => setCurrentPass(e.target.value)} />
                <p className="edit-doctor-body-new-password-title">New Password</p>
                <input type="password" placeholder='New Password' className="input" onChange={(e) => setNewPass(e.target.value)} />
                <p className="edit-doctor-body-confirm-new-password-title">Confirm New Password</p>
                <input type="password" placeholder='Confirm New Password' className="input" onChange={(e) => setConfirmPass(e.target.value)} />
                <div className="btn">
                    <button className="sec-button margin-r-8" onClick={() => navigate('/home')}>Back</button>
                    <button className="main-button" onClick={Edit}>Save</button>
                </div>
            </div>
        </div>
    )
}

EditDoctor.propTypes = {

}

export default EditDoctor
