import { chainService } from './Blockchain.service.js'
import BlockModel, { TRANSACTION_TYPE } from '../models/Block.model'
import DoctorModel from '../models/Doctor.model'

class DoctorService {

    /**
     * @type {DoctorModel[]}
     */
    doctors = []

    get allDoctorsJsonString() {
        return ''
    }


    /**
     * 
     * @param {Object} doctorJson
     */
    addNewRawDoctor = (doctorJson, done) => {

        done(true)
    }

    parseDoctorList = () => {
        this.doctors = chainService.chains.filter(e => {
            return e.type === TRANSACTION_TYPE.doctor
        }).map(e => {
            return DoctorModel.initFromJson(e.transaction)
        })
    }

    /**
     * 
     * @param {DoctorModel} doctorObject
     * @returns {BlockModel}
     */
    convertDoctorToBlock = (doctorObject) => {
        let block = new BlockModel()
        block.previousHash = chainService.lastBlock.computeHash
        block.transaction = doctorObject.toJson
        block.index = doctorObject.ID
        block.type = TRANSACTION_TYPE.doctor
        return block
    }

}

export let doctorService = Object.seal(new DoctorService())