import BlockModel from '../models/Block.model.js'

class BlockChainService {

    /**
     * @type {BlockModel[]} chains
     */
    chains = []

    constructor() {
        this.chains.push(BlockModel.createGenericBlock())
    }

    /**
     * @type {BlockModel}
     */
    get lastBlock() {
        return this.chains[this.chains.length - 1]
    }

    /**
     * 
     * @param {BlockModel} block 
     */
    addBlockToChain = (block) => {
        if (this.isBlockValid(block)) {
            this.chains.push(block)
            return true
        }

        return false
    }

    /**
    * 
    * @param {BlockModel} block 
    */
    isBlockValid = (block) => {
        if (block.previousHash !== this.lastBlock.computeHash) {
            return false
        }

        return true
    }

    allChainToJsonString = () => {
        return JSON.stringify({
            chains: this.chains
        })
    }

    /**
     * 
     * @param {Object[]} jsons 
     */
    syncChain = (jsons) => {
        this.chains = []
        for (let index = 0; index < jsons.length; index++) {
            const element = jsons[index];
            this.chains.push(BlockModel.initFromJson(element))
        }
    }

    allChainToJson = () => {
        let result = []
        this.chains.forEach(element => {
            result.push(element.toJson)
        });

        return result
    }

}



export let chainService = Object.seal(new BlockChainService())