import React, { useState, useEffect, useContext } from 'react';
import './home.scss';
import { useNavigate } from 'react-router-dom';
import { socketService } from '../../services/Socket.service'
import { patientService } from '../../services/Patient.service'
import { StoreContext } from '../../context/StoreContext'
import { doctorService } from '../../services/Doctor.service'

function Home() {
    const navigate = useNavigate();
    const [patients, setPatients] = useState([]);
    const [doctors, setDoctors] = useState([]);
    const [listPatient, setListPatient] = useState([]);
    const [desc, setDesc] = useState('Welcome doctor')

    const { sharingData, login, getLogin, username } = useContext(StoreContext)

    useEffect(() => {
        socketService.getListPatients(patients => {
            patientService.patients = patients
            setPatients(patientService.patients)
            patientService.patients.map(e => {
                if (e.assignedDoctor === username) {
                    if (listPatient) {
                        listPatient.push(e)
                    }
                    else setListPatient(e)
                }
            })
        })
    }, [])

    useEffect(() => {
        socketService.getListDoctors(doctors => {
            doctorService.doctors = doctors
            setDoctors(doctorService.doctors)
        })
    }, [])

    const toDetail = async (e) => {
        await sharingData(e)
        navigate('/bc-healthcare-client/patientdetail')
    }

    const logOut = async (e) => {
        await getLogin('false');
        navigate('/bc-healthcare-client/login')
    }

    return (
        <div className="patient">
            <div className="patient-container">
                <div className='header-view'>
                    <h1 className='margin-t-8'>BC healthcare system</h1>
                    <div className='desc-title'>{desc}</div>
                    <div className="logout">
                        <button onClick={() => navigate('/bc-healthcare-client/editdoctor')} className="logout-button margin-r-8">Change Password</button>
                        <button onClick={logOut} className="logout-button">Logout</button>
                    </div>
                </div>
                <div className="patient-area">
                    <div className="patient-list">
                        <div className="patient-list-bar">
                            <span className='patient-list-bar-title'>Patients List</span>
                            <span onClick={() => navigate('/bc-healthcare-client/addpatient')} className='patient-list-bar-icon'>New one</span>
                        </div>

                        <div className="patient-infos">
                            {listPatient ? listPatient.map(e => {
                                return (
                                    <div className="patient-info" key={e.ID}>
                                        <div className="patient-desc">
                                            <div className="patient-name">
                                                {e.name ? e.name : ""}
                                            </div>
                                            <div className="patient-gender">
                                                <span className='content'>{e.gender ? e.gender : ""}</span>
                                            </div>
                                            <div className="patient-dob">
                                                <span className='content mt-5'>{e.DOB ? e.DOB : ""}</span>
                                            </div>
                                            <div className="patient-address">
                                                <span className='content'>{e.address ? e.address : ""}</span>
                                            </div>
                                            {doctors.map(doctor => {
                                                if (doctor.username == e.assignedDoctor) {
                                                    return (
                                                        <div className="doctor" key={doctor.username}>
                                                            <span className="title">Doctor: </span>
                                                            <span className='content'>{doctor.name}</span>
                                                        </div>
                                                    )
                                                }
                                            })}
                                        </div>
                                        <div className="patient-avatar">
                                            <div className="patient-img">
                                            </div>
                                            <div className="patient-status">
                                                Active
                                            </div>
                                            <button className="main-button" onClick={(event) => toDetail(e.ID)}>Detail</button>
                                        </div>
                                    </div>
                                )
                            })
                                : "No Data"}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

Home.propTypes = {

}

export default Home