import { io } from "socket.io-client";
import { chainService } from "./Blockchain.service";
import BlockModel from '../models/Block.model'

// const host = "172.17.0.201:3000";
const host = "https://ancient-meadow-63310.herokuapp.com";

export const socket_observe_key = {
    // receive
    connected: 'connection',
    getSocketId: 'getId',
    disconnect: 'disconnect',

    getAddNewPatient: 'addNewPatient',
    getSyncData: 'getSyncData',
    //send
    sendNewPatientCreated: 'newPatientCreated',
    sendSyncData: 'syncData'
}

export class SocketService {

    socketIo = undefined
    myId = undefined
    isConnected = false

    constructor() {
        this.start()
    }

    // loginWithDoctorAccount = (username, password, result) => {
    //     this.socketIo.emit('smc', {
    //         creatorKey: 'none',
    //         smcKey: 'GetDoctorDetailSmartContract',
    //         socketId: this.myId,
    //         doctorUsername: username,
    //         doctorPassword: password
    //     })
    //     this.onLogin = result
    // }

    // loginWithPatientAccount = (ID, password, result) => {
    //     this.socketIo.emit('smc', {
    //         creatorKey: 'none',
    //         smcKey: 'PatientDetailSmartContract',
    //         socketId: this.myId,
    //         patientId: ID,
    //         password: password
    //     })
    //     this.onLoginPatient = result
    // }

    login = (username, password, result) => {
        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'LoginSmartContract',
            socketId: this.myId,
            userId: username,
            password: password
        })
        this.onLogin = result
    }

    /**
     * 
     * @param {String} data 
     * @param {String} socketId
     */
    sendData = (data, command, socketId = undefined) => {
        if (socketId) {
            this.socketIo.to(socketId).emit(command, data);
        }
        else {
            this.socketIo.emit(command, data);
        }
    }

    needSynchronizeChains = () => {
        this.socketIo.emit(socket_observe_key.getSyncData)
    }

    /**
     * process smart contract to request list of patients
     */
    getListPatients = (result) => {
        if (this.isConnected === false) {
            setTimeout(() => {
                this.getListPatients(result)
            }, 1000);
            return
        }

        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'AllPatientsSmartContract',
            socketId: this.myId
        })

        this.onGetListPatients = result
    }

    onGetListPatients = (result) => { }

    onLogin = (result) => { }

    // onLoginPatient = (result) => { }

    onPatientDetail = (data) => { }

    addNewPatient = (patient) => {
        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'AddNewPatientSmartContract',
            patient: patient
        })
    }

    getPatientDetail = (ID, result) => {
        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'PatientDetailSmartContract',
            socketId: this.myId,
            patientId: ID
        })
        this.onPatientDetail = result
    }

    //edit patient
    editPatient = (ID, oldPass, newPass, result) => {
        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'ChangePatientPasswordSmartContract',
            socketId: this.myId,
            userId: ID,
            oldPassword: oldPass,
            newPassword: newPass
        })
        this.onEditPatient = result
        console.log(ID, oldPass, newPass)
    }
    onEditPatient = (result) => { }

   

    //get list doctor
    getListDoctors = (result) => {
        if (this.isConnected === false) {
            setTimeout(() => {
                this.getListDoctors(result)
            }, 1000);
            return
        }
        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'GetAllDoctorSmartContract',
            socketId: this.myId
        })
        this.onGetListDoctors = result
    }

    onGetListDoctors = (result) => { }

    //edit doctor
    editDoctor = (username, oldPass, newPass, result) => {
        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'ChangeDoctorPasswordSmartContract',
            socketId: this.myId,
            userId: username,
            oldPassword: oldPass,
            newPassword: newPass
        })
        this.onEditDoctor = result
    }

    onEditDoctor = (result) => { }

    addNewPrescription = (prescription) => {
        this.socketIo.emit('smc', {
            creatorKey: 'none',
            smcKey: 'AddPrescriptionSmartContract',
            prescription: prescription,
            socketId: this.myId
        })
    }


    onSyncData = (data) => { }

    /**
     * 
     * @param {BlockModel} newBlock 
     */
    onReceiveNewPatient = (newBlock) => { }

    start = () => {
        console.log('Start socket ...');
        this.socketIo = io(host)

        this.socketIo.on("connect", () => {
            console.log('Socket connected to server');
            this.isConnected = true
        });

        this.socketIo.on('getId', data => {
            console.log('get my ID', data);
            this.myId = data
        })

        this.socketIo.on("disconnect", (reason) => {
            if (reason === "io server disconnect") {
                // the disconnection was initiated by the server, you need to reconnect manually
                this.socketIo.connect();
            }
            // else the socket will automatically try to reconnect
        });

        //receive sync signal
        this.socketIo.on(socket_observe_key.sendSyncData, (data) => {
            console.log('sync data: ', data);
            chainService.syncChain(data.data)
            this.onSyncData(data)
        })

        this.socketIo.on(socket_observe_key.sendNewPatientCreated, (data) => {
            const newBlock = BlockModel.initFromJson(data.data)
            chainService.addBlockToChain(newBlock)
            this.onReceiveNewPatient(newBlock)
        })

        this.socketIo.on('getChains', socketid => {
            // let transaction = TransactionModel.init(socketid, chainService.allChainToJson())

            // this.sendData(
            //     transaction.toJson(),
            //     socket_observe_key.sendSyncData
            // )
        })

        this.socketIo.on('getMaxChain', socketId => {
            // console.log('Some one need your chain', chainService.chains);
            // this.socketIo.emit('getMaxChain', TransactionModel.init(this.myId, { max: chainService.chains.length }))
        })

        /**
         * smart contract return value
         */
        this.socketIo.on('smc_return', transaction => {
            console.log('SMR return', transaction);
            if (transaction.data) {
                if (transaction.data.smcKey === 'AllPatientsSmartContract') {
                    this.onGetListPatients(transaction.data.patients)
                }

                if (transaction.data.smcKey === 'PatientDetailSmartContract') {
                    // this.onLoginPatient(transaction.data.patient)
                    this.onPatientDetail(transaction.data.patient)
                }

                if (transaction.data.smcKey === 'GetAllDoctorSmartContract') {
                    this.onGetListDoctors(transaction.data.doctors)
                }

                if (transaction.data.smcKey === 'GetDoctorDetailSmartContract') {
                    this.onLogin(transaction.data.doctor)
                }

                if (transaction.data.smcKey === 'LoginSmartContract') {
                    if (transaction.data.patient) {
                        // this.onLogin(transaction.data.patient)
                        this.onLogin('patient')
                    }
                    else if (transaction.data.doctor) {
                        // this.onLogin(transaction.data.doctor)
                        this.onLogin('doctor')
                    } else {
                        this.onLogin(false)
                    }
                }

                if (transaction.data.smcKey === 'ChangeDoctorPasswordSmartContract') {
                    this.onEditDoctor(transaction.data.doctor)
                }

                if (transaction.data.smcKey === 'ChangePatientPasswordSmartContract') {
                    this.onEditPatient(transaction.data.patient)
                }
            }
        })
    }

}

export let socketService = Object.seal(new SocketService())