import { encrypt } from '../utils/Encryptor.util'

export default class Patient {

    ID = null;
    name = null;
    DOB = null;
    address = null;
    gender = null;
    isDeleted = false;
    password = null;

    get jsonString() {
        return JSON.stringify(this.toJson)
    }

    get toJson() {
        return {
            ID: this.ID,
            name: this.name,
            DOB: this.DOB,
            address: this.address,
            gender: this.gender,
            isDeleted: this.isDeleted,
            // password: this.password
            password: encrypt(this.password, this.password)
        }
    }

    static initFromJson = (json) => {
        let patient = new Patient()
        patient.ID = json.ID
        patient.name = json.name
        patient.DOB = json.DOB
        patient.address = json.address
        patient.gender = json.gender
        patient.isDeleted = json.isDeleted
        patient.password = json.password
        return patient
    }

}