import React, { useContext } from 'react';
import './prescriptionDetail.scss';
import { useNavigate } from 'react-router-dom';
import { StoreContext } from '../../context/StoreContext'

function PrescriptionDetail() {

    const navigate = useNavigate();
    const { idPrescription, listPrescription, patient } = useContext(StoreContext)
    const prescriptionDetail = [];
    listPrescription.map(e => {
        if (e.ID === idPrescription) {
            prescriptionDetail.push(e)
        }
    })

    return (
        <div className='prescription-detail'>
            <div className="prescription-detail-container">
                <div className="prescription-detail-header">
                    <h1 className="prescription-detail-header-title">
                        Prescription Detail
                    </h1>
                    <button onClick={() => navigate('/bc-healthcare-client/patientdetail')} className="btn-back">
                        Back
                    </button>
                </div>
                <table className="prescription-detail-table">
                    {prescriptionDetail.map(e => {
                        return (
                            <tbody key={e.ID}>
                                <tr className='prescription-detail-id'>
                                    <th className='prescription-detail-id-title'>ID</th>
                                    <td className='prescription-detail-id-content'>{e.ID}</td>
                                </tr>
                                <tr className='prescription-detail-name'>
                                    <th className='prescription-detail-name-title'>Prescription Name</th>
                                    <td className='prescription-detail-name-content'>{e.name}</td>
                                </tr>
                                <tr className='prescription-detail-doctor'>
                                    <th className='prescription-detail-doctor-title'>Doctor</th>
                                    <td className='prescription-detail-doctor-content'>{e.doctor}</td>
                                </tr>
                                <tr className='prescription-detail-date'>
                                    <th className='prescription-detail-date-title'>Date</th>
                                    <td className='prescription-detail-date-content'>{e.createdDate}</td>
                                </tr>
                                <tr className='prescription-detail-patient-name'>
                                    <th className='prescription-detail-patient-name-title'>Patient Name</th>
                                    <td className='prescription-detail-patient-name-content'>{patient.name}</td>
                                </tr>
                                <tr className='prescription-detail-medication-dose'>
                                    <th className='prescription-detail-medication-dose-title'>Medication Dose</th>
                                    <td className='prescription-detail-medication-dose-content'>
                                        <table className='medication-detail'>
                                            <tbody>
                                                <tr className='medication-detail-title'>
                                                    <th className='medication-detail-class-title'>Medication Class</th>
                                                    <th className='medication-detail-name-title'>Medication Name</th>
                                                    <th className='medication-detail-route-title'>Route</th>
                                                    <th className='medication-detail-schedule-title'>Schedule</th>
                                                </tr>
                                                {e.medications ? e.medications.map(medication => {
                                                    return (
                                                        <tr className='medication-detail-content' key={medication.ID}>
                                                            <td className='medication-detail-class-value'>{medication.class}</td>
                                                            <td className='medication-detail-name-value'>{medication.name}</td>
                                                            <td className='medication-detail-route-value'>{medication.route}</td>
                                                            <td className='medication-detail-schedule-value'>{medication.schedule}</td>
                                                        </tr>
                                                    )
                                                }) :
                                                    <tr className='medication-detail-content' >
                                                        <td className='medication-detail-class-value'></td>
                                                        <td className='medication-detail-name-value'></td>
                                                        <td className='medication-detail-route-value'></td>
                                                        <td className='medication-detail-schedule-value'></td>
                                                    </tr>
                                                }
                                            </tbody>
                                        </table>
                                    </td>
                                </tr>
                            </tbody>
                        )
                    })}
                </table>
            </div>
        </div>
    )
}

PrescriptionDetail.propTypes = {

}

export default PrescriptionDetail