import MedicationModel from './Medication.model'
import { TimeUtil } from '../utils/Time.util'

export default class Prescription {

    ID = null;
    name = null;
    doctor = null;
    createdDate = null;
    patientID = null;

    /**
     * @type {MedicationModel[]} medications
     */
    medications = []

    constructor() {
        this.ID = TimeUtil.currentUTCTimestamp()
        this.createdDate = TimeUtil.currentUTCTimestamp()
    }

    get toJson() {
        const medicationJson = this.medications.map(e => {
            return e.toJson
        })

        return {
            ID: this.ID,
            name: this.name,
            doctor: this.doctor,
            createdDate: this.createdDate,
            patientID: this.patientID,
            medications: medicationJson
        }
    }


    static initFromJson = (json) => {
        let prescription = new Prescription()
        prescription.ID = json.ID
        prescription.name = json.name
        prescription.doctor = json.doctor
        prescription.createdDate = json.createdDate
        prescription.patientID = json.patientID
        prescription.medications = json.medications.map(e => {
            return MedicationModel.initFromJson(e)
        })

        return prescription
    }

}